header {
  width: 100vw;
  height: 100vh;
  background: #161616;
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(120deg, #161616, #2B2836);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}
header .header {
    width: 100%;
    height: 100%;
}
header .header .header-text {
      width: 800px;
      max-width: 90%;
      color: #fff;
      z-index: 2;
}
header .header .header-text h1 {
        font-size: 3.5em;
        line-height: 110%;
        padding-bottom: 20px;
}
header .header .header-text p {
        font-size: 1.5em;
        padding-bottom: 20px;
}
header .header .header-text button {
        background: #04AA51;
        line-height: 50px;
        height: 50px;
        border: none;
        width: 250px;
        color: #fff;
        font-size: 1.2em;
        font-weight: 900;
        border-radius: 4px;
        cursor: pointer;
}
header .header .header-text button:hover {
          background: #087038;
}
.bgpattern,
.bgpattern2,
.bgpattern3,
.bgpattern4 {
  width: 100%;
  height: 100%;
  background-size: 100%;
  background-position: top center;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  -webkit-animation-name: animaBg;
          animation-name: animaBg;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  background-attachment: fixed;
  opacity: .2;
}
@-webkit-keyframes animaBg {
from {
    background-position: 0% 5000px;
}
to {
    background-position: 0% -5000px;
}
}
@keyframes animaBg {
from {
    background-position: 0% 5000px;
}
to {
    background-position: 0% -5000px;
}
}
.bgpattern {
  background: url(assets/images/bg.svg) !important;
  -webkit-animation-duration: 200s;
          animation-duration: 200s;
}
.bgpattern2 {
  background: url(assets/images/bg2.svg) !important;
  -webkit-animation-duration: 200s;
          animation-duration: 200s;
}
.bgpattern3 {
  background: url(assets/images/bg3.svg) !important;
  -webkit-animation-duration: 150s;
          animation-duration: 150s;
}
.bgpattern4 {
  background: url(assets/images/bg4.svg) !important;
  -webkit-animation-duration: 120s;
          animation-duration: 120s;
}



